import React, { Component } from "react";
import TextField from "@material-ui/core/es/TextField/TextField";
import MenuList from "@material-ui/core/es/MenuList";
import {
  StyledPaper,
  StyledMenuItem,
  StyledSearchIcon,
  Wrapper
} from "./styled";

export default class Autocomplete extends Component {
  state = {
    inputVal: this.props.value,
    justUpdated: true
  };

  // componentDidUpdate() {
  //   const { value } = this.props;
  //
  //   this.state.inputVal !== value && this.setState({ inputVal: value });
  // }

  select(val) {
    this.setState({
      inputVal: val,
      justUpdated: true
    });

    this.props.onChange(val);
  }

  onInputChange(e) {
    this.setState({
      inputVal: e.target.value.toLowerCase(),
      justUpdated: false
    });
  }

  onKeyUp(e, options) {
    options[0] && e.key === "Enter" && this.select(options[0]);
  }

  render() {
    const { inputVal, justUpdated } = this.state;

    const options = this.props.options
      .filter(opt => new RegExp([...inputVal].join(".{0,3}")).test(opt))
      .filter(
        (_, _i, { length }) =>
          (inputVal.length >= 2 || length <= 10) && length >= 1
      );

    return (
      <Wrapper>
        <TextField
          margin="normal"
          variant="outlined"
          spellCheck="false"
          fullWidth
          value={inputVal}
          onChange={e => this.onInputChange(e)}
          onKeyUp={e => this.onKeyUp(e, options)}
        />
        <StyledSearchIcon color="#fff" />
        <StyledPaper hidden={justUpdated || options.length === 0}>
          <MenuList>
            {options.map(opt => (
              <StyledMenuItem
                disableGutters
                button
                onClick={() => this.select(opt)}
                key={opt}
                value={opt}
              >
                {opt}
              </StyledMenuItem>
            ))}
          </MenuList>
        </StyledPaper>
      </Wrapper>
    );
  }
}
