// @flow

import type { Element } from "react";
import styled from "styled-components";

import React, { Component, Children, cloneElement } from "react";
import Button from "@material-ui/core/es/Button";
import Typography from "@material-ui/core/es/Typography";
import Grid from "@material-ui/core/es/Grid";

import { TShirt } from "../components/TShirt";
import { DistrictSelect, PropertySelector } from "../components/controls";
import { colors, districts, sizes } from "../../data";
import { District } from "../contexts";
import Brkln from "../components/printParts/Brkln";
import NewLogo from "../components/printParts/NewLogo";
import { mainColor, secondColor } from "../theme";

type Props = {
  children?: Element<*>,
  title: string
};

const getPrintColor = color => {
  switch (color.value) {
    case "white":
      return mainColor;
    case "blue":
      return secondColor;
    case "red":
      return "#fff";
    default:
      return secondColor;
  }
};

const TShirtWrapper = styled.div`
  position: relative;
  //top: 0;
  //left: 0;
  width: 100vh;
  max-width: 100%;
  //overflow: hidden;

  &::after {
    content: "";
    display: block;
    padding-top: 100%;
  }
`;

const StyledTShirt = styled(TShirt)`
  position: absolute;
  max-width: none;
  left: 50%;
  top: 50%;
  transform: translate(-59%, -33%);
  width: 220%;
`;

const StyledBrkln = styled(Brkln)`
  position: relative;
  z-index: 2;
  display: block;
  width: calc(100% - 2em);
  height: auto;
  margin: 4em auto 1em;
`;

const StyledNewLogo = styled(NewLogo)`
  position: absolute;
  z-index: 2;
  display: block;
  width: calc(100% - 1em);
  height: auto;
  margin: 0.5em auto;
`;

export default class Simplicity extends Component<Props, *> {
  state = {
    district: districts[0],
    color: colors[0],
    size: sizes[2],
    shownInterface: false
  };

  tshirtRef: { current: any } = React.createRef();
  formOffsetRef: { current: any } = React.createRef();
  slideIntervalId = -1;

  startSlideShow() {
    let color = 1;
    this.slideIntervalId = window.setInterval(() => {
      this.setState({
        color: colors[color],
        district: districts[Math.round(Math.random() * districts.length - 2)]
      });
      color = color < colors.length - 1 ? color + 1 : 0;
    }, 2000);
  }

  stopSlideShow() {
    window.clearInterval(this.slideIntervalId);
    this.slideIntervalId = -1;
  }

  componentDidMount() {
    this.startSlideShow();

    const cb = () => {
      const element = this.formOffsetRef.current;

      if (
        element &&
        element.getBoundingClientRect().bottom - window.innerHeight < 100
      ) {
        window.removeEventListener("scroll", cb);
        this.stopSlideShow();
        this.setState({ shownInterface: true });
      }
    };

    window.addEventListener("scroll", cb);
  }

  componentWillUnmount() {
    this.stopSlideShow();
  }

  selectDistrict(selected: string) {
    this.setState({ district: selected });
  }

  selectColor(selected: string) {
    const result = colors.find(item => item.value === selected);
    this.setState({ color: result });
  }

  selectSize(selected: string) {
    const result = sizes.find(item => item.value === selected);
    this.setState({ size: result });
  }

  onClick() {
    const { color, district, size } = this.state;
    let print;

    try {
      print = (this.tshirtRef.current: any).getPrint();
    } catch (e) {}

    const args = {
      name: `${this.props.title.toUpperCase()} ${district}`,
      color: color.label,
      size: size.label,
      print
    };
    if ("txtlByu" in window) {
      window.txtlByu(...Object.values(args));
    } else {
      console.log(args);
    }
  }

  render() {
    return (
      <>
        <Typography component="h2" variant="h4" color="primary">
          <StyledNewLogo />
        </Typography>
        <Grid container justify="center">
          <Grid item xs={12} md={8}>
            <TShirtWrapper>
              <District.Provider
                value={{
                  district: this.state.district,
                  color: getPrintColor(this.state.color)
                }}
              >
                <StyledTShirt color={this.state.color.value}>
                  {Children.map(this.props.children, child =>
                    cloneElement(child, { ref: this.tshirtRef })
                  )}
                </StyledTShirt>
              </District.Provider>
            </TShirtWrapper>
          </Grid>
          <Grid item xs={12} md={4} style={{ maxWidth: "400px" }}>
            <Typography
              variant="h2"
              component="h2"
              style={{ marginTop: "15vh" }}
            >
              TXTL.ME
            </Typography>
            <Typography variant="h4" component="p">
                .
            </Typography>
            <StyledBrkln />
            <Typography variant="h4" component="p">
              #% .
            </Typography>
            <div
              style={{
                marginTop: "25vh",
                opacity: this.state.shownInterface ? 1 : 0,
                transition: "opacity 0.3s ease-in"
              }}
              ref={this.formOffsetRef}
            >
              <DistrictSelect
                value={this.state.district}
                values={districts.map(d => d.toLowerCase())}
                select={val => this.selectDistrict(val)}
              />
              <PropertySelector
                selected={this.state.color.value}
                values={colors}
                select={val => this.selectColor(val)}
              />
              <PropertySelector
                selected={this.state.size.value}
                values={sizes}
                select={val => this.selectSize(val)}
              />
              <Button
                onClick={() => this.onClick()}
                variant="contained"
                color="primary"
              >
                1500 
              </Button>
            </div>
          </Grid>
        </Grid>
      </>
    );
  }
}
